body {
  margin: 0;
  padding: 0;
}

.main {
  font-family: Roboto, sans-serif;
}

.mainbar {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 250px;
  right: 0px;
  background-color: rgb(236, 239, 241);
}

.header {
  height: 150px;
  background-color: #17bebb;
}

.content {
  overflow: scroll;
  position: absolute;
  top: 150px;
  left: 0px;
  right: 0px;
  bottom: 0px;
}

.sidebar {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  width: 250px;
  background-color: #2e282a;
}

.createSnippetInputSection {
  text-align: center;
}

.createSnippetInputSection input {
  padding: 10px;
  border: 1px solid rgb(216, 216, 216);
  border-radius: 4px;
  font-size: 20px;
  font-style: italic;
  font-weight: lighter;
  width: 400px;
  margin-bottom: 20px;
}

.createSnippetInputSection input::-webkit-input-placeholder {
  color: rgb(216, 216, 216);
  text-align: center;
}

.createSnippetInputSection input:-ms-input-placeholder {
  color: rgb(216, 216, 216);
  text-align: center;
}

.createSnippetInputSection input::-ms-input-placeholder {
  color: rgb(216, 216, 216);
  text-align: center;
}

.createSnippetInputSection input::placeholder {
  color: rgb(216, 216, 216);
  text-align: center;
}

/* LAYOUT */

.snippetOuterContainer {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
}

.snippetInnerContainer {
  position: relative;
  cursor: pointer;
  background-color: white;
  border: 1px solid rgb(216, 216, 216);
  overflow: hidden;
  -webkit-box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.09);
          box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.09);
}

.tile-layout .snippetOuterContainer {
  width: 25%;
  height: 150px;
  float: left;
}

.tile-layout .snippetInnerContainer {
  height: 130px;
}

.tile-layout .snippetOuter {
  height: 130px;
}

.snippetTitle {
  background-color: rgb(245, 245, 245);
  color: rgb(112, 112, 112);
  vertical-align: middle;
  padding: 5px;
  padding-left: 10px;
  height: 25px;
  line-height: 25px;
}

.snippetOuter pre[class*="language-"] {
  margin: 0px;
  border-radius: 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
}

.tile-layout .snippetOuter pre[class*="language-"] {
  height: 95px;
}



